<template>
    <el-menu :default-active="activeIndex" class="top_header" mode="horizontal" :ellipsis="false"
        @select="handleSelect">
        <el-menu-item index="0">
            <el-menu-item index="0">{{ "VulTrac" }}</el-menu-item>
        </el-menu-item>
        <div class="flex-grow" />
        <el-menu-item index="1">检测页</el-menu-item>
        <el-menu-item index="2" @click="toggleDark()">
            {{ isDark ? 'Dark' : 'Light' }}
        </el-menu-item>
    </el-menu>

    <div>
        <!-- 容器 -- header/main/footer结构 -->
        <el-container>
            <el-main class="main">
                <el-row :gutter="5">
                    <el-col :span="12">
                        <!-- 输入框 -->
                        <div class="inputArea" />
                        <el-input v-model="inputArea" maxlength="5000" rows="14" resize="none"
                            style=" font-size: 18px; height:390px" placeholder="请输入您需要检查的代码。" show-word-limit
                            type="textarea" class="inputArea" />
                        <!-- 确认上传 -->
                        <el-popover :visible="visible" placement="top" :width="160">
                            <p>请确认您上传的代码</p>
                            <div style="text-align: right; margin: 0">
                                <el-button size="small" text @click="visible = false">取消</el-button>
                                <el-button size="small" type="primary" @click="handleUpload">确认</el-button>
                            </div>
                            <template #reference>
                                <el-button @click="visible = true">确认上传</el-button>
                            </template>
                        </el-popover>
                    </el-col>
                    <el-col :span="12">
                        <!-- 输出位置 -->
                        <!--// v-html = 'mdData'-->
                        <el-card class="md-card"> 
                            <template #header>
                                <div class="card-header">
                                    <span>检测结果</span>
                                </div>
                            </template>
                            <div v-html="renderMarkdown(mdData)"></div>
                        </el-card>
                        <!-- 上传文件 -->
                        <el-upload class="upload-demo" drag action="http://localhost:5000/api/upload_file"
                            :multiple="false" :limit="1" @change="handleFileChange">
                            <el-icon class="el-icon--upload"><upload-filled /></el-icon>
                            <div class="el-upload__text">
                                Drop file here or <em>click to upload</em>
                            </div>
                            <!-- <template #tip>
                                <div class="el-upload__tip">
                                    files with a size less than 500kb
                                </div>
                            </template> -->
                        </el-upload>
                    </el-col>
                </el-row>
            </el-main>
        </el-container>

    </div>

</template>

<script lang="js" setup>
import { useDark, useToggle } from '@vueuse/core'
import { ref } from 'vue' //onMounted,
import { UploadFilled } from '@element-plus/icons-vue'
import axios from 'axios'
import { ElLoading } from 'element-plus'
import { marked } from 'marked'

// 暗色主题
const isDark = useDark()
const toggleDark = useToggle(isDark)

// 文本域
const inputArea = ref('')
const visible = ref(false)

// md 渲染
const mdData = ref('')

// 加载缓冲
let loading = null;

let startLoading = () => {
    loading = ElLoading.service({
        lock: true,
    })
}

let endLoading = () => {
    loading.close();
}

// 文件传输
const handleFileChange = (file) => {
    const formData = new FormData()
    formData.append('file', file.raw)

    startLoading();

    axios.post('http://localhost:5000/api/upload_file', formData)
        .then(response => {
            // 处理文件上传成功后的逻辑
            const file_path = response.data.file_path;
            sendFilePath(file_path);
        })
        .catch(error => {
            // 处理文件上传失败后的逻辑
            endLoading();
            console.error(error)
        })
};

// 向后端发送 file_path 的请求
const sendFilePath = (file_path) => {
    axios.post('http://localhost:5000/api/process', { file_path })
        .then(response => {
            // 处理发送成功后的逻辑
            endLoading();
            const data = response
            console.log(data);
            mdData.value = data.text;
        })
        .catch(error => {
            // 处理发送失败后的逻辑
            endLoading();
            console.error('Failed to send file path:', error);
        });
};

// 文本传输
const handleUpload = () => {
    const text = inputArea.value;

    startLoading();

    axios.post('http://localhost:5000/api/upload_text', { text })
        .then(response => {
            // 处理文本上传成功后的逻辑
            const file_path = response.data.file_path;
            sendTextPath(file_path);
        })
        .catch(error => {
            // 处理文本上传失败后的逻辑
            console.error(error);
            endLoading();
        });
    visible.value = false;
};

// 向后端发送 text_path 的请求
const sendTextPath = (file_path) => {
    axios.post('http://localhost:5000/api/process', { file_path })
        .then(response => {
            // 处理发送成功后的逻辑
            console.log(data);
            endLoading();
            const data = response.data;
            mdData.value = data.text;
        })
        .catch(error => {
            // 处理发送失败后的逻辑
            console.error('Failed to send text path:', error);
            endLoading();
        });
};

// 转换展示
const renderMarkdown = (text) => {
    return marked(text);
}

</script>

<style scoped>
.card-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  font-size: 14px;
}

.flex-grow {
    flex-grow: 1;
}

.md-card {

    height: 385px;
    /* 设置固定的高度 */
}
</style>